<template>
    <div>
        <div class="kd-page-title">
            {{ getTitle() }}
        </div>
        <div
            class="kd-page-content-wrapper wrap-info"
            v-if="step === 0"
        >
            <el-form
                class="uploadLabel"
                label-width="15vw"
            >
                <el-form-item label="名称：">
                    <el-radio-group v-model="delayTpe">
                        <el-radio
                            class="uploadChoose"
                            v-for="(item,index) in delayBusinessTypes"
                            :key="index"
                            :label="item.type"
                            border
                        >
                            {{ item.value }}
                        </el-radio>
                    </el-radio-group>
                </el-form-item>
                <el-form-item label="材料上传：">
                    <div class="imgListsWrap">
                        <div
                            class="uploadImg"
                            v-for="(item,index) in delayMaterialPhoto"
                            :key="index"
                        >
                            <img
                                class="preImg"
                                :src="item"
                                :key="index"
                            >
                            <i
                                @click="removeImg(index)"
                                class="delImg"
                            />
                        </div>
                        <div
                            class="uploadButton"
                            @click="goToPhoto"
                        >
                            <p :class="isLoad ? 'kedaIconfont kedaIcon-zhaoxiangji' : 'el-icon-loading'"/>
                        </div>
                    </div>
                </el-form-item>
            </el-form>
        </div>
        <div
            class="kd-page-content-wrapper wrap-img"
            v-if="step === 1"
        >
            <!--<object ref="objectElement" id="view1" type="application/x-eloamplugin" width="400" height="300" name="view"></object>-->
            <img
                :src="highPhotoImg"
                alt=""
                width="400"
                height="300"
            >
        </div>

        <div
            class="kd-page-content-wrapper wrap-img"
            v-if="step === 2"
        >
            <!--<object ref="objectElement" id="view1" type="application/x-eloamplugin" width="400" height="300" name="view"></object>-->
            <img
                :src="nowImg"
                alt=""
                width="400"
                height="300"
            >
        </div>

        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                v-if="step === 0"
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                v-if="step === 0"
                icon="kedaIcon-circleleft"
                text="上一步"
                :button-handle="goUp"
            />
            <kd-button
                v-if="step === 0"
                icon="kedaIcon-circleright"
                text="下一步"
                :button-handle="goNext"
                :but-disable="!delayTpe"
            />

            <kd-button
                v-if="step === 1"
                icon="kedaIcon-zhaoxiangji"
                text="拍照"
                :button-handle="startScan"
            />
            <kd-button
                v-if="step === 1"
                icon="kedaIcon-chexiao1"
                text="返回"
                :button-handle="goToIndex"
            />

            <kd-button
                v-if="step === 2"
                icon="kedaIcon-baocun"
                text="保存"
                :button-handle="save"
            />
            <kd-button
                v-if="step === 2"
                icon="kedaIcon-zhaoxiangji"
                text="重拍"
                :button-handle="goToPhoto"
            />
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';

export default {
    name: 'PhysicalEvidenceReason',
    data () {
        return {
            step: 0,
            nowImg: '',
            isLoad: false,
            delayMaterialPhoto: [],
            delayTpe: '',
            delayBusinessTypes: [
                {
                    type: '0',
                    value: '就业'
                },
                {
                    type: '1',
                    value: '服兵役'
                },
                {
                    type: '2',
                    value: '出国（境）'
                }
            ]
        };
    },
    computed: {
        ...mapState('permanent', {
            dialog: 'highPhotoDialog',
            lightWebSocket: 'lightWebSocket',
            highPhotoDev: 'highPhotoDev',
            highPhotoImg: 'highPhotoImg'
        }),
        ...mapState('physicalEvidence', {})
    },
    mounted () {
        setTimeout(() => {
            this.isLoad = true;
        }, 3000);
        this.dialog.html_loaded('one');
    },
    beforeDestroy () {
        // 关闭高拍仪主视频
        this.dialog.get_actionType('closeSignal');
    },
    methods: {
        ...mapMutations('common', ['changeStep', 'changeTipPage']),
        ...mapMutations('physicalEvidence', 'setPhysicalRes'),
        getTitle () {
            if (this.step === 0) {
                return '延期原因及证明';
            } else if (this.step === 1) {
                return '请将证明放置于照片识别区保持不动';
            } else if (this.step === 2) {
                return '拍摄完成';
            }
        },
        // 进入第一步
        async goToIndex () {
            this.step = 0;
            this.$wsSend(this.lightWebSocket, 'lightCtrl@B6:OFF');
        },
        // 进入第二步
        goToPhoto () {
            if (this.isLoad) {
                this.step = 1;
                this.$wsSend(this.lightWebSocket, 'lightCtrl@B6:ON');
            }
        },
        goUp () {
            this.changeStep('up');
        },
        async goNext () {
            this.changeStep('down');
        },
        // 拍照，进入第三部
        startScan () {
            this.nowImg = this.highPhotoImg;
            this.step = 2;
        },
        // 保存数据，并返回第一步
        save () {
            const res = {
                applyName: '赵丽颖',
                types: '六年免检',
                serialNumber: 4231424234,
                applyTime: '2019-4-1'
            };
            this.setPhysicalRes(res);
            this.step = 0;
        },
        removeImg (index) {
            this.delayMaterialPhoto.splice(index, 1);
            this.setDelayMaterialPhoto(this.delayMaterialPhoto);
        }

    }
};
</script>

<style lang="scss" scoped>
    .bg {
        width: 34.37vw;
        height: 26.56vh;
        border-radius: 0.5vh;
        background: url("../../../../../assets/images/cardno_bg.png");
        background-size: 100% 100%;
        margin: 0 auto;
    }

    .wrap-img {
        display: flex;
        justify-content: center;
        align-items: center;

        img {
            width: 400px;
            height: 300px;
            background: #fff;
        }
    }

    .wrap-info {
        display: flex;
        flex-direction: column;
        justify-content: center;

        ::v-deep .el-form-item__content {
            margin-right: 10vw;
        }

        .imgListsWrap {
            display: flex;

            .uploadImg {
                margin-right: 20px;
                border-radius: 6px;
                border: 1PX solid rgba(242, 249, 255, 1);/*no*/
                width: 13vw;
                height: 13vw;
                position: relative;

                .preImg {
                    width: 100%;
                    height: 100%;
                    border-radius: 6px;
                }

                .delImg {
                    position: absolute;
                    top: -15px;
                    right: -15px;
                    z-index: 3;
                    width: 30px;
                    height: 30px;
                    background: url("../../../../../assets/images/del.png");
                    background-size: 100% 100%;
                }
            }

        }

        .uploadButton {
            background: rgba(255, 255, 255, 0.8);
            border-radius: 6px;
            border: 1PX solid rgba(242, 249, 255, 1);/*no*/
            width: 13vw;
            height: 13vw;
            text-align: center;

            p {
                font-size: 5vh;
                color: #85BAF0;
                text-align: center;
                line-height: 13vw;
            }
        }
    }
</style>
