<template>
    <div>
        <div class="kd-title-content-wrapper kd-page-padding wrap-info">
            <text-table :data="personInfo"/>
        </div>
        <div class="kd-foot-button kd-foot-button-position" v-if="!$store.state.common.visibleTip">
            <kd-button
                icon="kedaIcon-policework"
                text="返回首页"
                type-class="home"
            />
            <kd-button
                icon="kedaIcon-circleleft"
                text="上一步"
                :button-handle="preStep"
            />
            <kd-button
                icon="kedaIcon-circleright"
                text="下一步"
                :button-handle="nextStep"
            />
        </div>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import textTable from '@/components/text-table';

export default {
    name: 'PhysicalEvidenceConfirmInfo', // 确认信息
    components: { textTable },
    computed: {
        ...mapState('physicalEvidence', {
            personInfo: 'personInfo'
        })
    },
    methods: {
        ...mapMutations('common', ['changeStep']),
        nextStep () { // 下一步
            this.changeStep('down');
        },
        preStep () { // 上一步
            this.changeStep('up');
        }

    }
};
</script>

