<template>
    <div>
        <server-info v-if="currentStep === steps.findIndex(step=>step.key === 'serverInfo')"/>
        <identify-card-page v-if="currentStep === steps.findIndex(step=>step.key === 'identifyCardPage')"/>
        <face-yitu v-if="currentStep === steps.findIndex(step=>step.key === 'faceYitu')"/>
        <contrast v-if="currentStep === steps.findIndex(step=>step.key === 'contrast')"/>
        <confirm-info v-if="currentStep === steps.findIndex(step=>step.key === 'confirmInfo')"/>
        <check-phone v-if="currentStep === steps.findIndex(step=>step.key === 'checkPhone')"/>
        <reason v-if="currentStep === steps.findIndex(step=>step.key === 'reason')"/>
        <complete
            v-if="currentStep === steps.findIndex(step=>step.key === 'complete')"
            :data="physicalRes"
        />
    </div>
</template>

<script>
import { mapMutations } from 'vuex';
import identifyCardPage from '@/view/public/before-work/identify-card-page';
import faceYitu from '@/view/public/before-work/face-yitu';
import contrast from '@/view/public/before-work/contrast';
import serverInfo from '@/components/server-info';
import confirmInfo from './components/confirm-info';
import checkPhone from './components/check-phone';
import reason from './components/reason';
import complete from '@/components/complete';

export default {
    name: 'PhysicalEvidenceIndex', // 身体证明
    components: {
        identifyCardPage,
        faceYitu,
        contrast,
        serverInfo,
        confirmInfo,
        checkPhone,
        complete,
        reason
    },
    methods: {
        ...mapMutations('common', ['setSteps'])
    },
    mounted () {
        this.setSteps([
            {
                name: '业务须知',
                isActive: true,
                visible: true,
                key: 'serverInfo'
            },
            {
                name: '身份识别',
                isActive: false,
                visible: true,
                key: 'identifyCardPage'
            },
            {
                name: '身份识别',
                isActive: false,
                visible: false,
                key: 'faceYitu'
            },
            {
                name: '身份识别',
                isActive: false,
                visible: false,
                key: 'contrast'
            },
            {
                name: '信息确认',
                isActive: false,
                visible: true,
                key: 'confirmInfo'
            },
            {
                name: '手机校验',
                isActive: false,
                visible: true,
                key: 'checkPhone'
            },
            {
                name: '选择原因',
                isActive: false,
                visible: true,
                key: 'reason'
            },
            {
                name: '完成提交',
                isActive: false,
                visible: true,
                key: 'complete'
            }
        ]);
    }
};

</script>
